import { useGetToken } from '@cetus/hooks/src/useToken'
import { Token } from '@cetus/types'
import envConfigs from '@cetus/types/src/config/envConfigs'
import { fixCoinType } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { useCallback, useEffect, useState } from 'react'

export default function useQuoteWhiteTokenList() {
  const [quoteWhiteTokenList, setQuoteWhiteTokenList] = useState<Token[]>([])
  const { getTokenInfo } = useGetToken()

  useEffect(() => {
    setQuoteWhiteTokenList(fetchQuoteWhiteTokenList())
  }, [])

  /**
   * èŽ·å–åˆ›å»ºæ± å­quote list ç™½åå•
   * @returns
   */
  const fetchQuoteWhiteTokenList = () => {
    const list: Token[] = []
    envConfigs.create_pool_quote_coin_list.forEach(coinType => {
      const info = getTokenInfo(coinType)
      if (info) {
        list.push(info)
      }
    })

    return list
  }

  const isWhiteQuoteToken = useCallback(
    (coinType?: string) => {
      if (coinType) {
        return quoteWhiteTokenList.find(token => fixCoinType(token.coin_type) === fixCoinType(coinType)) !== undefined
      }

      return false
    },
    [quoteWhiteTokenList]
  )

  return {
    isWhiteQuoteToken,
    quoteWhiteTokenList
  }
}
